import React, { useEffect, useState } from 'react';
import './Content.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import postsData from './posts.json'; // Импортировать данные JSON
import Auth from './Login';
import Login from './Login';
import Register from './Register';
import Settings from './Settings';

const Content = () => {
    const [posts, setPosts] = useState([]);

    useEffect(() => {
        const fetchPosts = async () => {
            const response = await fetch('./posts.json');
            const data = await response.json();
            setPosts(data);
        };
        fetchPosts();
    }, []);

    const allMaps = [...postsData.subs, ...postsData.articles, ...postsData.news];

    return (
        <div className='content'>
            <div className='HomeTitle'>
                <Routes>
                    <Route path="/" element={<h1>Главная</h1>} />
                    <Route path="/subs" element={<h1>Подписки</h1>} />
                    <Route path="/articles" element={<h1>Статьи</h1>} />
                    <Route path="/news" element={<h1>Новости</h1>} />
                    <Route path="/login" element={<h1>Авторизация</h1>} />
                </Routes>
            </div>

            <div className='feed'>
                <Routes>
                    <Route
                        path="/"
                        element={
                            allMaps.map((product, index) => (
                                <div className='productCard' key={index}>
                                    <h3>{product.title}</h3>
                                    {/* Используем require для изображений из src */}
                                    <img src={require(`${product.img}`)} alt='фото' />

                                    <p>{product.description}</p>
                                    <div className='footer'>
                                        <div>
                                            <div>
                                                <img src={require(`${product.community_image}`)} alt='Сообщество' />
                                                <div>
                                                    <h3>{product.community_name}</h3>
                                                    <p>{product.community_subs}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className='sub-btn'>
                                            <a href='#'>
                                                <p>Подписаться</p>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    />
                    <Route
                        path="/subs"
                        element={
                            postsData.subs.map((product, index) => (
                                <div className='productCard' key={index}>
                                    <h3>{product.title}</h3>
                                    {/* Используем require для изображений из src */}
                                    <img src={require(`${product.img}`)} alt='фото' />

                                    <p>{product.description}</p>
                                    <div className='footer'>
                                        <div>
                                            <div>
                                                <img src={require(`${product.community_image}`)} alt='Сообщество' />
                                                <div>
                                                    <h3>{product.community_name}</h3>
                                                    <p>{product.community_subs}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className='sub-btn'>
                                            <a href='#'>
                                                <p>Подписаться</p>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    />
                    <Route
                        path="/articles"
                        element={
                            postsData.articles.map((product, index) => (
                                <div className='productCard' key={index}>
                                    <h3>{product.title}</h3>
                                    {/* Используем require для изображений из src */}
                                    <img src={require(`${product.img}`)} alt='фото' />

                                    <p>{product.description}</p>
                                    <div className='footer'>
                                        <div>
                                            <div>
                                                <img src={require(`${product.community_image}`)} alt='Сообщество' />
                                                <div>
                                                    <h3>{product.community_name}</h3>
                                                    <p>{product.community_subs}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className='sub-btn'>
                                            <a href='#'>
                                                <p>Подписаться</p>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    />
                    <Route
                        path="/news"
                        element={
                            postsData.news.map((product, index) => (
                                <div className='productCard' key={index}>
                                    <h3>{product.title}</h3>
                                    {/* Используем require для изображений из src */}
                                    <img src={require(`${product.img}`)} alt='фото' />

                                    <p>{product.description}</p>
                                    <div className='footer'>
                                        <div>
                                            <div>
                                                <img src={require(`${product.community_image}`)} alt='Сообщество' />
                                                <div>
                                                    <h3>{product.community_name}</h3>
                                                    <p>{product.community_subs}</p>
                                                </div>
                                            </div>
                                        </div>
                                        <div className='sub-btn'>
                                            <a href='#'>
                                                <p>Подписаться</p>
                                            </a>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    />
                    <Route path='/auth' element={
                        <Auth />
                    } />
                    {/* <Route path='/register' element={
                        <Register />
                    } />
                    <Route path='/settings' element={
                        <Settings />
                    } /> */}
                </Routes>
            </div>
        </div>
    );
};

export default Content;
