import React, { useState, useEffect } from 'react';
import usersData from './users.json'; // Импортировать JSON файл

const Auth = () => {
    const [login, setLogin] = useState('');
    const [password, setPassword] = useState('');
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [error, setError] = useState('');
    const [currentUser, setCurrentUser] = useState(null);

    useEffect(() => {
        // Проверить наличие сохраненного пользователя при загрузке страницы
        const savedUser = localStorage.getItem('currentUser');
        if (savedUser) {
            setIsLoggedIn(true);
            setCurrentUser(JSON.parse(savedUser));
        }
    }, []);

    // Авторизация
    const handleLogin = () => {
        const user = usersData.users.find(
            (u) => u.login === login && u.password === password
        );
        if (user) {
            setIsLoggedIn(true);
            setCurrentUser(user);
            localStorage.setItem('currentUser', JSON.stringify(user));
            // Переход на страницу настроек
            window.location.href = '/auth';
        } else {
            setError('Неверный логин или пароль');
        }
    };

    // Регистрация
    const handleRegister = () => {
        const userExists = usersData.users.some((u) => u.login === login);
        if (userExists) {
            setError('Пользователь с таким логином уже существует');
            return;
        }

        // Создание нового пользователя
        const newUser = {
            id: usersData.users.length + 1,
            login: login,
            password: password,
        };

        // Добавление нового пользователя в JSON (на клиенте)
        usersData.users.push(newUser);
        localStorage.setItem('currentUser', JSON.stringify(newUser));
        setIsLoggedIn(true);
        setCurrentUser(newUser);
        // Переход на страницу настроек
        window.location.href = '/auth';
    };

    // Выход
    const handleLogout = () => {
        setIsLoggedIn(false);
        setCurrentUser(null);
        localStorage.removeItem('currentUser');
        // Переход на страницу входа
        window.location.href = '/auth';
    };

    return (
        <div>
            {isLoggedIn ? (
                <div>
                    <h1>Настройки</h1>
                    <h3>Имя: {currentUser.login}</h3>
                    <h3>Подписок: 3</h3>
                    <h3>Дата регистрации: 12.06.24</h3>
                    <br />
                    <button onClick={handleLogout}>Выйти</button>
                </div>
            ) : (
                <div>
                    <h2>Вход или регистрация</h2>
                    <input
                        type="text"
                        placeholder="Логин"
                        value={login}
                        onChange={(e) => setLogin(e.target.value)}
                    />
                    <input
                        type="password"
                        placeholder="Пароль"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                    <button onClick={handleLogin}>Вход</button>
                    <button onClick={handleRegister}>Регистрация</button>
                    {error && <p>{error}</p>}
                </div>
            )}
        </div>
    );
};

export default Auth;
